let drawOnLoad = function(id) {
    let path = document.querySelector(`#${id}`);
    let pathLength = path.getTotalLength();
    path.style.strokeDasharray = pathLength + ' ' + pathLength;
    path.style.strokeDashoffset = pathLength;

    setTimeout(()=>{
        $(".main").addClass("_active")
    },1400)

    setTimeout(()=>{
        path.style.strokeDashoffset = 0
    },2000)
}

drawOnLoad("path-1")
drawOnLoad("path-2")


let drawFirst = function(id,nextBlock,delay) {
    let path = document.querySelector(`#${id}`);
    let pathLength = path.getTotalLength();
    path.style.strokeDasharray = pathLength + ' ' + pathLength;
    path.style.strokeDashoffset = pathLength;
    window.addEventListener("scroll", function(e) {
        let scrollPercentage = (document.documentElement.scrollTop + document.body.scrollTop) / (($(nextBlock).offset().top + 100))
        // scrollPercentage = scrollPercentage * 1.25
        // console.log(nextBlock,scrollPercentage)
        let drawLength = pathLength * scrollPercentage;
        if(delay){
            scrollPercentage = scrollPercentage * 1.5
            let drawLength = pathLength * scrollPercentage;
            setTimeout(()=>{
                path.style.strokeDashoffset = pathLength - drawLength;
            },1000)
        } else{
            path.style.strokeDashoffset = pathLength - drawLength;
        }

    });
}

drawFirst("path-3",".stat",false)
drawFirst("path-4",".info-slider",false)
drawFirst("path-5",".info-slider",false)
drawFirst("path-6",".footer",true)
drawFirst("path-7",".footer",true)
drawFirst("path-8",".footer",false)
